import apiHelper from '../utils/helpers';

export default {
  signIn({ email, password }) {
    return apiHelper.post('auth/login', {
      email,
      password,
    });
  },
  checkToken() {
    const token = document.cookie.replace(/(?:(?:^|.*;\s*)hexToken\s*=\s*([^;]*).*$)|^.*$/, '$1');
    return apiHelper.post('auth/check', {
      api_token: token,
    });
  },
};
