<template>
  <div>
    <Loading v-if="isLoading" :isLoading="isLoading" />
    <div class="container py-5">
      <div class="card card-body">
          <form class="w-100 form-signin" @submit.prevent="signin">
            <h1 class="h3 mb-3 font-weight-normal">
              請先登入
            </h1>
            <div class="form-group row">
              <label for="inputEmail">Email address</label>
              <input id="inputEmail" v-model="user.email" type="email"
                class="form-control" placeholder="Email address"
                required autofocus>
            </div>
            <div class="form-group row">
              <label for="inputPassword">Password</label>
              <input id="inputPassword" v-model="user.password" type="password" class="form-control"
              placeholder="Password" required>
            </div>
            <button class="btn btn-outline-primary" type="submit">
              登入
            </button>
          </form>
      </div>
    </div>
  </div>
</template>

<script>
import authorizationAPI from '../../apis/authorization';
import Loading from '../../components/Loading.vue';

export default {
  name: 'Login',
  data() {
    return {
      user: {
        email: '',
        password: '',
      },
      token: '',
      isLoading: false,
    };
  },
  components: {
    Loading,
  },
  methods: {
    async signin() {
      this.isLoading = true;
      const response = await authorizationAPI.signIn({
        email: this.user.email,
        password: this.user.password,
      });
      const { data } = response;
      if (!data.success) {
        throw new Error(data.message);
      }
      document.cookie = `hexToken=${data.token};expires=${new Date(data.expired * 1000)};`;
      this.isLoading = false;
      this.$router.push('/admin/products');
    },
    signout() {
      document.cookie = 'hexToken=;expires=;';
      console.log('token 已清除');
    },
  },
};
</script>

